/**
 * CB CTA Block Styles
 *
 * @package  cb-identity2025
 */

.cb-cta {
  position: relative;
  background-color: var(--col-white);
  // border-top: 1px solid rgba(255, 255, 255, 0.1);
  // border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  isolation: isolate;
  overflow: hidden;
  color: var(--col-primary-black);
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
    background: var(--_bg-url) center / cover no-repeat fixed;
  }

  &__title {
    font-size: var(--fs-850);
    font-weight: var(--fw-light);
    line-height: 1.1;
    color: var(--col-lime-900);
  }
  &__content {
    font-size: var(--fs-600);
    font-weight: var(--fw-light);
  }

  &__image {
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
  }

  &__clip-group {
    mask-size: contain;
    mask-repeat: no-repeat;
    mask-position: center;
    overflow: hidden;
  }
