  border: none;
  text-decoration: none;
  transition: background-color 0.3s ease;
  isolation: isolate;
  text-align: center;
  vertical-align: middle;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: auto;
}
.id-button::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: var(--col-purple-900);
  opacity: 1;
  transition: transform 0.3s ease;
  transform: rotate(-3deg);
  z-index: -1;
}
.id-button:hover {
  color: #fff;
  text-decoration: none;
}
.id-button:hover::before {
  transform: rotate(0deg);
}

/* Base button */
.btn,
.btn a {
  --_bg: var(--col-white);
  --_fg: var(--col-primary);
  --_bd: var(--col-white);
  display: inline-flex;
  align-items: center;
  gap: var(--btn-gap);
  text-decoration: none !important;
  text-transform: none;
  padding: var(--btn-py) var(--btn-px);
  border-radius: 6px;
  border: var(--btn-border-w) solid var(--_bd);
  background: var(--_bg);
  color: var(--_fg);
  font-family: var(--ff-body);
  font-weight: var(--fw-ui);
  font-size: var(--fs-200);
  line-height: 1;
  transition: background-color var(--btn-transition), border-color var(--btn-transition), color var(--btn-transition), transform var(--btn-transition);
}
.btn:hover,
.btn a:hover {
  background-color: var(--_fg);
  color: var(--_bg);
  border-color: var(--_bg);
}

.btn-id-outline-green {
  --_bg: transparent;
  --_fg: var(--col-green-400);
  --_bd: var(--col-green-400);
  border-radius: 0;
  height: 34px;
}
.btn-id-outline-green:hover {
  background-color: var(--col-green-400);
  color: var(--col-primary-black);
  border-color: var(--col-green-400);
}

.btn:focus-visible {
  outline: 2px solid var(--col-accent-400);
  outline-offset: 2px;
}

.btn:active {
  transform: translateY(1px);
  box-shadow: var(--shadow-active);
}

/* Disabled */
.btn[disabled],
.btn:disabled {
  opacity: 0.55;
  cursor: not-allowed;
  box-shadow: none;
}

/* Icon affordance (SVG or <i>) */
.btn > svg,
.btn > i {
  inline-size: 1em;
  block-size: 1em;
}

/* Motion preference */
@media (prefers-reduced-motion: reduce) {
  .btn {
    transition: none;
  }
}
