@import "typography";
@import "colours";
@import "buttons";
@import "header";
@import "footer";
@import "news";
@import "a11y";
@import "search";
@import "blocks/blocks";

body {
  background-color: var(--col-neutral-50);
}

@media (min-width: 992px) {
  .cols-lg-2 {
    columns: 2;
  }
  .cols-lg-3 {
    columns: 3;
  }
}

.sticky {
  position: sticky;
  top: 0; // var(--h-top);
  @media (min-width: 992px) {
    top: 0; //var(--h-top-desktop);
  }
  background-color: var(--col-neutral-50);
  z-index: 1000;
}

// Adjust fixed header when admin bar is present
.admin-bar header.fixed-top {
  top: 32px; // Admin bar height
}

.anchor {
  display: block;
  position: relative;
  top: calc(-1 * var(--h-top) - 1rem);
  visibility: hidden;
  @media (min-width: 992px) {
    top: calc(-1 * var(--h-top-desktop) - 1rem);
  }
}

// Gravity Forms checkbox spacing
.gform-field-label--type-inline {
  margin-left: 0.5rem;
}

#breadcrumbs {
  font-size: var(--text-sm);
  a {
    text-decoration: none;
    font-weight: var(--fw-600);
    color: var(--col-neutral-900);
  }
}

.wp-block-group {
  &:not(.alignfull) {
    @extend .container;
    & .container {
      padding-inline: 0;
    }
  }
}

.container {
  padding-inline: 1.5rem !important;
}
.container .container {
  padding-inline: 0 !important;
}

.id-container {
  width: min(100%, 1920px);
  margin: 0 auto;
}

// main {
//   margin-top: var(--h-top);
// }

.ratio-9x16 {
  --bs-aspect-ratio: calc(16 / 9 * 100%); /* = 177.77% */
}

.service-parents {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;
  display: flex;
  flex-direction: column;
  width: fit-content;
  min-width: 50%;
  li {
    font-size: var(--fs-body);
    border-top: 1px solid rgba(255, 255, 255, 0.4);
    display: inline-block;
    width: 100%;
    a {
      text-decoration: none;
      display: block;
      color: var(--col-green-500);
      transition:
        background-color 0.3s,
        color 0.3s;

      &:hover {
        text-decoration: none;
        background-color: hsl(var(--hsl-neutral-800) / 0.8);
      }
    }
  }
  li:last-child {
    border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  }
}

