}

.service-parents {
  list-style: none;
  padding-left: 0;
  margin-top: 3rem;
  margin-bottom: 0;
  display: flex;
  flex-direction: column;
  width: fit-content;
  min-width: 50%;
  li {
    font-size: var(--fs-body);
    border-top: 1px solid var(--col-lime-900);
    display: inline-block;
    width: 100%;
