.cb-hero-prop-cta {
  background: var(--grad-main);
  color: var(--col-primary-black);

  // animated bit
  &__title {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    line-height: 1;
    padding-left: 11vw;
    padding-right: 11vw;

    padding-left: 0; // Let Bootstraps col gutter handle left spacing
    padding-right: 0; // Avoid double padding from the grid
    overflow: visible; // Prevent accidental clipping when rotated

    .line {
      position: relative;
      display: inline-block;
      margin: 0.1em 0;
      transform-origin: 25% center; /* slightly deeper pivot for safety */
    }

    /* middle line gets even deeper pivot to stop clipping */
    .line:nth-child(2) {
      transform-origin: 35% center;
    }

    .bar {
      position: absolute;
      inset: -0.1em -0.6em; /* keeps bars wider than text */
      background: var(--col-white);
      z-index: 1;
      opacity: 0;
    }

    .text {
      position: relative;
      z-index: 2;
      color: var(--col-lime-900);
      font-size: 5.5vw;
      font-weight: var(--fw-light);
      opacity: 0;
      text-align: left;
    }

    /* horizontal offset to match layout */
    .line:nth-child(1) {
      margin-left: 0em;
    }
    .line:nth-child(2) {
      margin-left: -3.6em;
    }
    .line:nth-child(3) {
      margin-left: 1.8em;
    }

    /* fine-tuned vertical spacing */
    .line:nth-child(2) {
      margin-top: 0.1em;
    }
    .line:nth-child(3) {
      margin-top: -0.25em;
    }

    /* final text rotations */
    .text1 {
      transform: rotate(-3deg);
    }
    .text2 {
      transform: rotate(5deg);
    }
    .text3 {
      transform: rotate(-6deg);
    }
  }
  // end animated bit

  &__content-heading {
    font-size: var(--fs-800);
    line-height: 1;
    font-weight: var(--fw-light);
    padding-block: 0.15em 0.1em;
    border-top: 1px solid var(--col-lime-900);
    border-bottom: 1px solid var(--col-lime-900);
  }
  &__content {
    font-size: var(--fs-500);
  }
}
